 import React from 'react'
import {Card,CardContent, Typography,Grid} from '@material-ui/core'
import Countup from 'react-countup'
import './Cards.css'
const Cards = (props) => {
    console.log("abc",props.data)
    if(!props.data.confirmed){
        return "Loading....."
    }
    return(
        <div className="container">
            <Grid container justify="centre">
                <Grid  item component={Card} xs={12} md={3} className="card infected">
                    <CardContent>
                        <Typography color="textSecondary" gutterBottom>Confirmed</Typography>
                        <Typography variant='h5'>
                            <Countup
                                start={0}
                                end={props.data.confirmed}
                                duration={2.5}
                                separator=","

                            />
                        </Typography>
                        <Typography color="textSecondary">Last updated:{props.data.lastupdatedtime}</Typography>
                        <Typography variant="body2">Number of confirmed cases</Typography>
                    </CardContent>
                </Grid>
                <Grid  item component={Card} xs={12} md={3} className="card active">
                    <CardContent>
                        <Typography color="textSecondary" gutterBottom>Active</Typography>
                        <Typography variant='h5'>
                            <Countup
                                start={0}
                                end={props.data.active}
                                duration={2.5}
                                separator=","

                            />
                        </Typography>
                        <Typography color="textSecondary">Last updated:{props.data.lastupdatedtime}</Typography>
                        <Typography variant="body2">Number of active cases</Typography>
                    </CardContent>
                </Grid>
                <Grid item component={Card}xs={12} md={3} className="card deaths">
                    <CardContent>
                        <Typography color="textSecondary" gutterBottom>Deaths</Typography>
                        <Typography variant='h5'>
                            <Countup
                                    start={0}
                                    end={props.data.deaths}
                                    duration={2.5}
                                    separator=","

                                />
                        </Typography>
                        <Typography color="textSecondary">Last updated:{props.data.lastupdatedtime}</Typography>
                        <Typography variant="body2">Number of deaths</Typography>
                    </CardContent>
                </Grid>
                <Grid item component={Card} xs={12} md={3} className="card recovered">
                    <CardContent>
                        <Typography color="textSecondary" gutterBottom>Recovered</Typography>
                        <Typography variant='h5'>
                            <Countup
                                        start={0}
                                        end={props.data.recovered}
                                        duration={2.5}
                                        separator=","

                                    />
                        </Typography>
                        <Typography color="textSecondary">Last updated:{props.data.lastupdatedtime}</Typography>
                        <Typography variant="body2">Number of recoveries</Typography>
                    </CardContent>
                </Grid>
            </Grid>
        </div>
    )
}

export default Cards;