import React from 'react'
import {Cards,Chart,StatePicker} from './Components'
import './App.module.css'
import {FetchData} from './api'



class App extends React.Component{

    state={
        data:null,
        states:[],
        stateIndex:0
    }


    async componentDidMount(){
        const data = await FetchData()
        
        console.log("xyz",data.states)

        if (data){
            this.setState({
                data:data,
                states:data.states
            })
        }
        
    }

    stateChange = async(st) => {
        console.log(typeof(st))
        if(st){
            const ind = this.state.states.findIndex((state)=> state.state === st)
            console.log(ind)
            this.setState({
                stateIndex:ind
            })
        }
       
    }

    render(){
        return(
            <div className="container">
                <h1>Covid 19 cases in India</h1>
                <StatePicker stateList={this.state.states} handleStateChange={this.stateChange} />
                {this.state.data ? <Cards data={this.state.states[this.state.stateIndex]}/> : null}
                <Chart />
            </div>
        )
    }
}

export default App;