import React from 'react'
import{FormControl,NativeSelect} from '@material-ui/core'

const StatePicker = (props) => {
    console.log("shshshshs",props)
    return(
        <FormControl>
            <NativeSelect onChange={(e) => props.handleStateChange(e.target.value)}>
                {props.stateList.map((states,index) => <option key={index} value={states.state}>{states.state}</option>)}
            </NativeSelect>
        </FormControl>
    )
}

export default StatePicker;